    },
   data() {
      return {
      
      }
   },
   created() {
   },
   methods: {
       
   }
}
</script>

<style lang="scss">
body{
    background: rgb(248, 247, 247);
}
.Search{
    width: 100%;
    .search_one{
        background: white;
        margin-top: 0.8rem;
        width: 100%;
        padding: 0.2rem 0.5rem;
        font-size: 0.3rem;
        >input{
            width: 80%;
            font-size: 0.25rem;
            padding: 0.1rem 0rem 0.1rem 0.2rem;
            outline: none;
            border-radius: 2%;
            background: #f2f2f2;
            border: none;
            font-weight: bold;
        }
        >button{
            width: 15%;
            background: #3399ff;
            color: white;
            border: none;
            border-radius: 5px;
            line-height: 0.55rem;
            margin-left: 0.3rem;
        }
